/* Less than 576px */
.nopm {
    padding: 0px;
    margin: 0px;
}

#title {
    color: white;
    font-size: 40px;
}

#subtitle {
    color: white;
    font-size: 20px;
}

#logotitle {
    position: absolute;
    z-index: 2;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 60px;
    text-align: center;
    left: 5%;
    right: 5%;
    
    text-shadow: 0px 0px 3px rgba(0,0,0,0.6), 
                 0px 0px 3px rgba(0,0,0,0.6), 
                 0px 0px 3px rgba(0,0,0,0.6),
                 0px 0px 3px rgba(0,0,0,0.6),
                 0px 0px 4px rgba(0,0,0,0.6),
                 0px 0px 4px rgba(0,0,0,0.6);
}

#bg {
    width: 100vw;
    height: auto;
}

#bgContainer {
    position: relative; 
    width: 100vw; 
    height: 20vh;
    overflow: hidden;
}
  



/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { 
    #title {
        font-size: 50px;
    }

    #logotitle {
        top: 60px;
        left: 100px;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #bg {
        /* Set rules to fill background */
        min-height: 100%;
        min-width: 1024px;
          
        /* Set up proportionate scaling */
        width: 100%;
        height: auto;
          
        /* Set up positioning */
        position: fixed;
        top: -100px;
        left: 0;
    }
    
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {

}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {


    #bgContainer {
        position: relative; 
        width: 100vw; 
        height: 40vh; 
        overflow: hidden;
    }

    
    #logotitle {
        top: 120px;
        left: 100px;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
      
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
    #bg {
        /* Set rules to fill background */
        min-height: 100%;
        min-width: 1024px;
          
        /* Set up proportionate scaling */
        width: 100%;
        height: auto;
          
        /* Set up positioning */
        position: fixed;
        top: -200px;
        left: 0;
    }
}