#nav-button {
    width: calc(100vw - 50px);
    height: 40px;

    display: flex;
    align-items: center;

    margin-top: 10px;
    padding-left: 15px;
    
    /*background-color: #bbb;*/
    cursor: pointer;
}

#nav-button:hover {
    color:#ac0505;
}

#nbar {
    margin-left: 18px;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { 

}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {

    #nav-button {
        width: 15vw;
        height: 40px;

        display: flex;
        justify-content: center;
        align-items: center;

        padding-left: 0px;
        
        cursor: pointer;
    }

    #nbar {
        display: flex;
        justify-content: space-evenly;
    }
}

